.movieImage {
  width: 100%;
  height: 400px;
  border-bottom: 3px solid rgb(114, 17, 17);
  cursor: pointer;
}

.movieGallery {
  display: flex;
  max-width: 1000px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
  padding: 20px;
  background-color: rgb(216, 155, 75);
  border-radius: 4px;
  box-shadow: 0 0.3em 1em #000;
  border: 5px solid rgb(114, 17, 17);
  background-image: url('../SharedLayout/timeless-treasures-cats-in-the-library-fabric-Lg.jpg');
}


.movieGalleryLabel {
  display: block;
  text-align: center;
  padding: 8px;
  font-weight: 700;
  font-size: 36px;
  color: rgb(114, 17, 17);
  font-family: 'Comic Sans MS';
}

.movieItem {
  width:200px;
  background-color: rgb(216, 155, 75, 0.8);
  border: 3px solid rgb(114, 17, 17);
  color: rgb(114, 17, 17);
  font-weight: 700;
  border-radius: 4px;
  font-family: 'Comic Sans MS';
  display: flex;
  flex-direction: column;
  align-items: center;
  transition-property: transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  padding: 5px;
}

.movieItem:hover {
  transform: scale(1.03);
  box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.3),
    0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 10px 12px -6px rgba(0, 0, 0, 0.4);
}

.galleryFrame{
  position: relative;
  max-width: 1000px;
  margin-right: auto;
  margin-left: auto;
}